import axios from 'axios'
import qs from 'qs';
import {baseURL} from 'api/config';
import {getLimited,setLimited,getLimitedUrl,setLimitedUrl,setToken,getToken} from 'common/js/auth';



let config={
  loginUrl:'',  //
  loginApi:`${baseURL}/wechatmini/login`,    //API


  logoutApi:`${baseURL}/logout`,  //API
  indexUrl:'' //
};
const service = axios.create({
  baseURL,
  timeout: 15000,               //
  withCredentials: true
});


let search=location.search.substring(1);
let searchArr=search.split('=');
setToken(searchArr[1]);



service.interceptors.request.use(config => {  // request
   if (config.headers['Content-Type'] === "multipart/form-data") {
     config.data = qs.stringify(config.data);
   }
  if (getToken()) {
   config.headers['x-auth-token'] = getToken(); // token 
   }

  return config
}, error => {
  Promise.reject(error)
});


service.interceptors.response.use(  // respone
  response => {
    let request = response.request;

    if (request.responseURL === config.loginApi && request.status === 200 ) {  //

      // setToken(response.headers['x-auth-token']);

     /* if(getLimited()==='true'){
        setLimited('false');
        location.href=getLimitedUrl();
      }
      else{
        location.href=config.indexUrl;
      }*/
    }
    else if (request.responseURL === config.logoutApi && request.status === 200 ) {   //
      location.href=config.loginUrl;
    }
    return response.data;
  },
  error => {


    if (!error.response) {
     //alert('');
      // 
      return ;
    }
    switch (error.response.status){
      case 401:
      /* if (location.href === config.loginUrl) {    //401
       }
       else {    //
       setLimited('true');
       setLimitedUrl(location.href);
       }*/
      break;
      case 403:     //403
        this.$vux.alert.show({
          title:'',
          content: ''
        });
      //
      break;
    }



    return Promise.reject(error)
  }
);
export default service



