import {mapGetters, mapMutations} from 'vuex';
import Number  from 'base/number/number';
import Alert from 'base/alert/alert';
import {setListMixin} from 'common/js/mixin';
import {changeShopNumber} from 'common/js/util';
export default {
  data(){
    return {
      content: '',
      cancelBtnText: '',
      alert: {
        state: true,
        text: '',
        cancelBtnText: ''
      },
      showFlag: false
    }
  },
  components: {
    Number,
    Alert
  },
  mixins:[setListMixin],
  props: {
    list: {
      type: Array,
      default: []
    }
  },
  methods: {
    confirm(){
      this.setShopList([]);
      this.setList([]);
    },
    clearShop(){
      this.$refs.alert.show();
    },
    coverHide(){
      this.$emit('coverHide');
    },
    show(){
      this.showFlag = true;
    },
    hide(){
      this.showFlag = false;
    },
    numChange(number, id){
      let shopList=changeShopNumber(this.shopList,number,id);
      this.setShopList(shopList);
      this.setList(shopList,this.$route.name);
    },
    price(price){
      return ` ${price}`
    },
    ...mapMutations({
      setShopList: 'SET_SHOP_LIST'
    })
  },
  computed: {
    ...mapGetters([
      'shopList'
    ])
  }
}



